@import url('https://fonts.googleapis.com/css?family=Indie+Flower');

*{
font: 40px 'Indie Flower', cursive;
}

body {
  font: 50px 'Indie Flower', cursive;
  margin: 50px;
}

ol, ul {
  padding-left: 50px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 50px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 175px;
  font-weight: bold;
  line-height: 34px;
  height: 200px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 200px;
}

.square.highlight {
  background: #ddd
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 50px;
}

.move-list-item-selected{
  float: left;
  font-weight: bold;
}
.my-button{
  margin-left: 25px;
}